/* src/App.css */
@keyframes colorCycle {
  0% { background-color: #32b414; }  /* Dark Orange/Brown */
  16% { background-color: #4e12db; }   /* Dark Red */
  32% { background-color: #666600; }  /* Dark Yellow/Olive */
  48% { background-color: #003300; }  /* Dark Green */
  64% { background-color: #003333; }  /* Dark Cyan */
  80% { background-color: #000033; }  /* Dark Blue */
  100% { background-color: #330033; } /* Dark Magenta */
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  animation: colorCycle 90s infinite; /* Slowed down the animation */
  background-size: 400% 400%;
}

.container {
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
}

h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.countdown {
  font-size: 4rem;
  color: #007bff;
  font-weight: bold;
}

@media (max-width: 600px) {
  h1 {
    font-size: 1.5rem;
  }

  .countdown {
    font-size: 3rem;
  }
}
